.product-store_cards-view {
    width: 350px;
    height: 335px;
    padding: 0.5rem 1.3rem 0 1.3rem;
    border-radius: 5px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  }
  
  .store__products_cards-view li {
    padding: 1rem;
  }
  
  .product-store__body {
    position: relative;
    height: 100%;
  }
  
  .product-store__header {
    position: relative;
    z-index: 2;
    padding: 1rem 0 0.5rem 0;
    text-align: center;
    background-color: #f5f5f5;
  }
  
  .product-store__header > *:not(:last-child) {
    margin-bottom: 0.8rem;
  }
  
  .product-store__picture-card {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 300px;
    top: 0;
    text-align: center;
    overflow: hidden;
  }
  
  .product-store__picture-card img {
    max-width: 100%;
    width: 100%;
  }
  
  .product-store__footer {
    position: absolute;
    z-index: 2;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0.5rem 1rem 1.5rem 1rem;
    background-color: #f5f5f5;
  }
  
  .product-store__btn_cards-view {
    flex: 1 1 auto;
  }
  
  @media (max-width: 420px) {
    .store__products_cards-view li {
      padding-left: 0;
      padding-right: 0;
    }
  
    .product-store_cards-view {
      max-width: 350px;
      width: 100%;
      margin: 0 auto;
    }
  }